import React, {Component} from "react";
// import avatar from './assets/etapro.jpg';
// import resume from './assets/resume.pdf'
import './assets/dema.css';
import avatar from './assets/demapro.jpg';
import MediaQuery from 'react-responsive';
import { withAlert } from 'react-alert'
import {demahbd}  from './assets/demahbd.js';

import Cake from './MyDemaCake'

class MyDema extends Component {

    constructor(props){
        super(props);
        this.state = {
            isMobile:false,
            isLogin:false,
            value: '',
            alertText:''
        };
        this.handleChange = this.handleChange.bind(this);
        this.checkLogin = this.checkLogin.bind(this);

    }

    componentDidMount() {

        if(this.state.isLogin)
            setTimeout(function(){
                demahbd();
            },1000);

    }

    handleChange(event) {
        this.setState({value: event.target.value});
    }
    checkLogin(event){
        if(this.state.value === "0712"){
            this.setState({isLogin:true});
            this.setState({alertText:"A pass was submitted correctly"});
            // this.props.alert.show('A pass was submitted correctly')
        }
        else{
            this.setState({alertText:"Нууц үг буруу байна"});
            // alert('A pass was submitted wrongly: ' + this.state.value);
            // this.props.alert.show('A pass was submitted wrongly');
        }
        event.preventDefault();
    }
    render(){
        if(!this.state.isLogin)
        return(
                    <div>
                        <MediaQuery minWidth={992}>
                            <div className="container">
                                <div className="spacer60"></div>
                                <div className="spacer60"></div>
                                    <div className="row justify-content-md-center">
                                        <div className="col-6 col-md-4 align-middle mx-auto">
                                        <div className="demapro align-middle">
                                            <img src={avatar} className="center-block rounded-circle"  alt="..."/>
                                        </div>
                                            <div className="center-block text-center">
                                                    <div>Энэ хүн бол нууц үгээ оруулаарай :)</div>
                                                    <form onSubmit={this.checkLogin}>
                                                      <p className="hbd text-secondary">Hint: Бидний анх уулзсан өдөр</p>
                                                      <div className="form-group">
                                                        <input type="password" className="form-control" name="password" value={this.state.value} onChange={this.handleChange} placeholder="1210 etc.."/>
                                                      </div>
                                                      <p className="hbd text-info">{this.state.alertText}</p>
                                                      <input type="submit" className="btn btn-dark" value="Нэвтрэх" />
                                                    </form>
                                            </div>
                                        </div>
                                    </div>
                            </div>
                        </MediaQuery>
                        <MediaQuery maxWidth={992}>
                         <div className="container">
                            <div className="spacer60"></div>
                            <div className="row justify-content-md-center text-center">
                                <div className="col-9 col-md-6 align-middle mx-auto">
                                    <div className="demapro align-middle">
                                        <img src={avatar} className="center-block rounded-circle"  alt="..."/>
                                    </div>
                                    <div className="spacer15"></div>
                                    <div className="center-block text-center">
                                            <div>Энэ хүн бол нууц үгээ оруулаарай :)</div>
                                            <form onSubmit={this.checkLogin}>
                                              <p className="hbd text-secondary">Hint: Бидний анх уулзсан өдөр</p>
                                              <div className="spacer15"></div>
                                              <div className="form-group">
                                                <input type="password" className="form-control" name="password" value={this.state.value} onChange={this.handleChange} placeholder="1210 etc.."/>
                                              </div>
                                              <p className="hbd text-info">{this.state.alertText}</p>
                                              <input type="submit" className="btn btn-dark" value="Нэвтрэх" />
                                            </form>
                                    </div>
                                    <div className="spacer30"></div>
                                </div>

                            </div>
                         </div>

                        </MediaQuery>
                    </div>
);
        else
        {
            return(
                    <Cake />
                );

            // demahbd();
        }
    }

}


export default withAlert(MyDema)



