import React, { Component } from 'react';
// import logo from './logo.svg';
import ReactGA from 'react-ga';
import {Route,BrowserRouter,Redirect,Switch} from 'react-router-dom';

// import './App.css';
import Profile from "./components/Profile";
import Home from "./components/Home";
import Contact from "./components/Contact";
import Mydema from "./components/MyDema";
import Mydema21 from "./components/MyDema21";


ReactGA.initialize('UA-130719503-1');
//ReactGA.pageview(window.location.pathname + window.location.search);
ReactGA.pageview('/hbd-princess');
ReactGA.pageview('/mydema');

class App extends Component {
  render() {

      return (
        <BrowserRouter>
           <Switch>
              <Route exact path="/" component={Home}/>
              <Route exact path="/profile" component={Profile}/>
              <Route exact path="/contact" component={Contact}/>
              <Route exact path="/hbd-princess" component={Mydema}/>
              <Route exact path="/mydema" component={Mydema21}/>
              <Redirect to="/" />
          </Switch>
        </BrowserRouter>
      );
    }
}

export default App;



