import React, {Component} from "react";
import avatar from './assets/mylove.jpg';
import bgpic from './assets/demabg.png';
import demasong1 from './assets/dema1.mp3';
// import resume from './assets/resume.pdf'
import {demalove}  from './assets/dema.js';
import './assets/dema21.css';
import MediaQuery from 'react-responsive';

export default class MyDema21 extends Component {

    componentDidMount() {
    }
    componentWillUnount() {
      this.stopMusic();
    }

    constructor(props) {
        super(props);

        var today = new Date();
        var birthDate = new Date("12/10/1997");
        var age = today.getFullYear() - birthDate.getFullYear();
        var m = today.getMonth() - birthDate.getMonth();
        if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate()))
            age = age - 1;
        var n = age - 20

        this.state = {
            isAudio: false,
            thisDate: n,
            alertText: "Зүрхэн дээр дараарай!",
        };
        this.url = demasong1;
        //console.log(this.audio);
        // this.audio = "";
        // this.url = demasong;
        this.audio = new Audio(this.url);
    }

    // onload = () => {
    //         var script = document.createElement("script");
    //         script.type = "text/javascript";
    //         script.src = "http://jsonip.appspot.com/?callback=DisplayIP";
    //         document.getElementsByTagName("head")[0].appendChild(script);
    //     function DisplayIP(response) {
    //         console.log("Your IP Address is " + response.ip);
    //     }
    // }


    clickHeart = () => {
      this.setState({ alertText: "" })
      this.playMusic();
      // this.startSong();
      demalove();
    }

    playMusic = () =>
    {
        // console.log("Came here");
        this.playPromise = this.audio.play();

      // Pause when the Promise is resolved. This MIGHT be
      //  immediate (synchronous)!
        this.playPromise.then(() => {
            this.audio.play();
            // console.log("Promise orj irsen");
          })
          // Safety first!
          .catch(e => {
            console.log("aldaa")
          });
    }

    // playMusic = async () => {

    //     console.log("started audio" + this.state.isAudio);
    //     // console.log(this.audio);
    //     // console.log("Started:"+this.audio.paused);
    //     this.setState({buttonName:"pause"});
    //     this.audio.play();
    // }

    stopMusic = async () => {
        console.log("stopped audio");
        this.audio.pause();
        console.log("Paused:"+this.audio.paused);
    }



    render(){
        return(
                <div className="demabg">
                <MediaQuery minWidth={992}>
                    <div className="cover super-centered-container">
                        <canvas id="can">Get a browser</canvas>
                        <div className="overlay-static">
                            <div className="inline-blocks">
                                <h2 className="twenty-one block">2</h2>
                                <img className="image block" src={avatar} alt="..."/>
                                <h2 className="twenty-one block"> + {this.state.thisDate }</h2>
                            </div>
                            <h1 className="bday">Happy Birthday <a className="dema-a" href={bgpic} target="_blank">Dema </a>
                            <button type="button" className="btn btn-danger btn-circle btn-lg" ref={input => this.inputElement = input} onClick={this.clickHeart}>
                            <i className="fa fa-heart"></i>
                            </button>
                            </h1>
                            <h6 className="alertText text-info">{this.state.alertText } </h6>
                        </div>
                    </div>
                </MediaQuery>
                <MediaQuery maxWidth={992}>
                    <div className="cover super-centered-container">
                        <canvas id="can">Get a browser</canvas>
                        <div className="overlay-static-mobile">
                            <div className="inline-blocks inline-blocks-mobile">
                                <h2 className="twenty-one-mobile block">2</h2>
                                <img className="image-mobile block-mobile" src={avatar} alt="..."/>
                                <h2 className="twenty-one-mobile block">+</h2>
                                <h2 className="twenty-one-mobile block">{this.state.thisDate }</h2>
                            </div>
                            <h1 className="bday-mobile">Happy Birthday <a className="dema-a" href={bgpic} target="_blank">Dema </a>
                            <button type="button" className="btn btn-danger btn-circle btn-lg" ref={input => this.inputElement = input} onClick={this.clickHeart}>
                            <i className="fa fa-heart"></i>
                            </button>
                            </h1>
                            <h6 className="alertText-mobile text-info">{this.state.alertText}</h6>
                        </div>
                    </div>
                </MediaQuery>

                </div>
            );
    }


}



